.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  clip: rect(0 0 0 0);
  overflow: hidden; }

.page-body {
  min-height: 100%;
  min-width: 100%;
  background-color: #ffffff;
  font-family: "Open Sans", "Arial", sans-serif; }

.page-header {
  position: relative; }

.page-header__about-form-wrapper {
  background: #1f1f1f url("../img/background-desktop-1x.jpg") no-repeat;
  background-position: 50% 0;
  background-size: auto; }

.page-header__container {
  max-width: 874px;
  margin: -51px auto 0;
  display: grid;
  grid-template-columns: auto auto;
  padding-top: 276px;
  padding-bottom: 240px; }

@media (max-width: 1023px) {
  .page-header__about-form-wrapper {
    background-image: url("../img/background-tablet-1x.jpg"); }
  .page-header__container {
    max-width: 100%;
    grid-template-columns: auto;
    grid-template-rows: auto auto;
    padding: 0;
    margin: 0; } }

@media (max-width: 767px) {
  .page-header__about-form-wrapper {
    background-image: url("../img/background-mobile-1x.jpg"); } }

.navigation {
  display: grid;
  margin: 0 auto -51px;
  max-width: 874px; }

.navigation__button-menu {
  display: none; }

.navigation__list {
  margin: 0;
  padding: 9px 19px;
  display: flex;
  justify-content: space-between;
  border-bottom: 1px solid rgba(255, 255, 255, 0.2);
  box-sizing: border-box; }
  .navigation__list li {
    list-style-type: none;
    display: flex; }
    .navigation__list li a {
      text-decoration: none;
      font-weight: 300;
      font-size: 16px;
      line-height: 200%;
      letter-spacing: -0.005em;
      color: rgba(255, 255, 255, 0.7); }

@media (max-width: 1023px) {
  .navigation {
    margin: 0;
    display: grid;
    max-width: 100%; }
  .navigation__button-menu {
    display: block;
    justify-self: end;
    position: relative;
    width: 50px;
    height: 50px;
    background-color: #0ad9c6;
    border: 0;
    cursor: pointer;
    border-radius: 0; }
    .navigation__button-menu::before {
      content: "";
      position: absolute;
      top: 18px;
      right: 14px;
      width: 22.5px;
      height: 1px;
      border-radius: 1px;
      background-color: #000000;
      box-shadow: 0 5.5px 0 0 #000000, 0 11.5px 0 0 #000000; }
  .navigation__list {
    margin: 0;
    padding: 0;
    display: flex;
    flex-direction: column;
    border-bottom: none; }
    .navigation__list li {
      min-height: 60px;
      width: 100%;
      box-sizing: border-box;
      padding: 16px;
      background-color: #f6f6f6;
      margin-bottom: 30px; }
      .navigation__list li a {
        font-size: 20px;
        line-height: 140%;
        color: #48494d; } }

.about h2 {
  font-weight: bold;
  font-size: 46px;
  line-height: 139%;
  color: #ffffff;
  letter-spacing: -0.005em; }

.about p {
